// App.js - BLAPL Loyalty App Prototype
// Based on Master Spec (see separate canvas)
// Focus: structure + in‑memory store + routing + core flows (check-in, coupons, admin)

/*
 Key Implementation Notes
 ------------------------
 - Internal check-in interval: 156 hours (6d12h). User-facing copy: "7 days".
 - Stackable coupons every 5 valid check-ins.
 - QR scan expects constant string 'bla-poker-checkin'.
 - Lightweight auth (in-memory users) with localStorage persistence layer.
 - Seeded admins: shawn@tjspecialty.com, jodi@tjspecialty.com, kevin@tjspecialty.com all pwd '123456'.
 - Admin capabilities: view users, redeem coupon, delete check-in (any), soft delete / restore user, rename user.
 - Local persistence: users + auditLog + auth session stored in localStorage (keys: blapl.users, blapl.audit, blapl.auth).
 - Toast notification system for success / error / info messages.
 - Accessibility & basic error handling stubs included.
*/

import React, { useState, useEffect, useRef, useCallback, useContext } from 'react';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { Html5Qrcode } from 'html5-qrcode';

// ---- Constants ----
const CHECKIN_INTERVAL_HOURS = 156; // internal
const CHECKIN_INTERVAL_MS = CHECKIN_INTERVAL_HOURS * 3600 * 1000;
const QR_CONSTANT = 'bla-poker-checkin';
const COUPON_MODULO = 5;
const STORAGE_USERS = 'blapl.users';
const STORAGE_AUDIT = 'blapl.audit';
const STORAGE_AUTH = 'blapl.authUserId';

// ---- Utility Helpers ----
const now = () => Date.now();
const hoursSince = (ts) => (now() - ts) / 3600000;
const safeParse = (raw, fallback) => {
  // Return fallback if nothing to parse or parse result is null
  if (!raw) return fallback;
  try {
    const parsed = JSON.parse(raw);
    return parsed == null ? fallback : parsed;
  } catch {
    return fallback;
  }
};

function formatRemainingDetailed(lastTs) {
  if (!lastTs) return 'Ready';
  const remainingMs = CHECKIN_INTERVAL_MS - (now() - lastTs);
  if (remainingMs <= 0) return 'Ready';
  const hours = Math.ceil(remainingMs / 3600000);
  if (hours < 24) return `${hours} hour${hours === 1 ? '' : 's'} remaining`;
  const days = Math.ceil(hours / 24);
  if (days === 1) return 'Less than 1 day';
  return `${days} days remaining`;
}

let _idCounter = 0; const genId = (p='id') => `${p}_${(++_idCounter).toString(36)}`;

const seedAdmins = [
  { email: 'shawn@tjspecialty.com', displayName: 'Shawn', isAdmin: true },
  { email: 'jodi@tjspecialty.com', displayName: 'Jodi', isAdmin: true },
  { email: 'kevin@tjspecialty.com', displayName: 'Kevin', isAdmin: true }
];

function createSeedUsers() {
  return seedAdmins.map(a => ({
    id: genId('u'),
    email: a.email,
    passwordHash: '123456',
    displayName: a.displayName,
    isAdmin: a.isAdmin,
    isActive: true,
    lastCheckInAt: 0,
    totalCheckIns: 0,
    couponsAvailable: 0,
    couponHistory: [],
    checkIns: [],
  }));
}

// ---- Toast System (react-toastify) ----
// All notifications now handled by react-toastify's <ToastContainer> and toast.* methods.

// Example usage in your components:
// toast.success('Check‑in recorded');
// toast.error('Invalid QR code');
// toast.info('Logged out');


// No need for a <ToastContext> or <ToastProvider> here.
// Make sure you have <ToastContainer /> rendered at the root (in index.js).

// ---- App Component ----
function App() {
  // Add background logo class to body when app mounts
  useEffect(() => {
    document.body.classList.add('app-bg');
    return () => document.body.classList.remove('app-bg');
  }, []);
  const [view, setView] = useState('scan');
  return (
    <AppProvider>
      <AuthGate>
        <NavBar current={view} onChange={setView} />
        {view === 'scan' && <ScanView />}
        {view === 'progress' && <ProgressView />}
        {view === 'leaderboard' && <LeaderboardView />}
        {view === 'admin' && <AdminView />}
      </AuthGate>
    </AppProvider>
  );
}

// ---- App Data Context ----
const AppContext = React.createContext(null);

function loadInitialUsers() {
  const raw = localStorage.getItem(STORAGE_USERS);
  if (!raw) return createSeedUsers();
  const data = safeParse(raw, []);
  if (!Array.isArray(data) || !data.length) return createSeedUsers();
  return data;
}
function loadInitialAudit() {
  return safeParse(localStorage.getItem(STORAGE_AUDIT), []);
}
function loadInitialAuth() {
  return localStorage.getItem(STORAGE_AUTH);
}

function AppProvider({ children }) {
  const [users, setUsers] = useState(loadInitialUsers);
  const [authUserId, setAuthUserId] = useState(loadInitialAuth);
  const [auditLog, setAuditLog] = useState(loadInitialAudit);

  // ——— Helper Functions ———
  const findUser = id => users.find(u => u.id === id);
  const updateUser = (id, updater) =>
    setUsers(prev => prev.map(u => u.id === id ? updater(u) : u));

  const addUser = (email, password, displayName) => {
    const exists = users.some(u => u.email.toLowerCase() === email.toLowerCase());
    if (exists) throw new Error('Email already exists.');
    const nu = {
      id: genId('u'),
      email,
      passwordHash: password,
      displayName,
      isAdmin: false,
      isActive: true,
      lastCheckInAt: 0,
      totalCheckIns: 0,
      couponsAvailable: 0,
      couponHistory: [],
      checkIns: []
    };
    setUsers(prev => [...prev, nu]);
    commitAudit('user.create', { userId: nu.id, email }, authUserId);
    return nu;
  };

  const softDeleteUser = id => {
    const target = findUser(id);
    if (target?.isAdmin) throw new Error('Cannot delete an admin');
    updateUser(id, u => ({ ...u, isActive: false }));
    commitAudit('user.softDelete', { userId: id }, authUserId);
  };

  const restoreUser = id => {
    updateUser(id, u => ({ ...u, isActive: true }));
    commitAudit('user.restore', { userId: id }, authUserId);
  };

  const renameUser = (id, newName) => {
    updateUser(id, u => ({ ...u, displayName: newName }));
    commitAudit('user.rename', { userId: id, newName }, authUserId);
  };

  const redeemCoupon = userId => {
    updateUser(userId, u => {
      if (u.couponsAvailable <= 0) return u;
      const idx = u.couponHistory.findIndex(c => !c.redeemedAt);
      if (idx === -1) return u;
      const updatedHistory = [...u.couponHistory];
      updatedHistory[idx] = { ...updatedHistory[idx], redeemedAt: now() };
      commitAudit('coupon.redeem', { userId, couponId: updatedHistory[idx].id }, authUserId);
      return { ...u, couponsAvailable: u.couponsAvailable - 1, couponHistory: updatedHistory };
    });
  };

  // Hardened check-in logic
  const lastActionRef = useRef(0);
  const lastScanValRef = useRef('');
  const lastScanTsRef = useRef(0);

  const performCheckIn = useCallback((userId, scanValue = '') => {
    const nowTs = now();
    if (nowTs - lastActionRef.current < 1200) throw new Error('BUSY');
    if (
      scanValue &&
      scanValue === lastScanValRef.current &&
      nowTs - lastScanTsRef.current < 2500
    ) {
      throw new Error('DUPLICATE');
    }
    lastActionRef.current = nowTs;
    lastScanValRef.current = scanValue;
    lastScanTsRef.current = nowTs;

    setUsers(prev =>
      prev.map(u => {
        if (u.id !== userId) return u;
        if (u.lastCheckInAt) {
          const delta = nowTs - u.lastCheckInAt;
          if (delta < 0) {
            commitAudit(
              'clock.anomaly',
              { userId, last: u.lastCheckInAt, now: nowTs },
              authUserId
            );
            throw new Error('TIME_ANOMALY');
          }
          if (delta < CHECKIN_INTERVAL_MS) throw new Error('TOO_SOON');
        }
        const newCheckIn = { id: genId('ci'), ts: nowTs };
        const newCheckIns = [...u.checkIns, newCheckIn];
        const totalCheckIns = u.totalCheckIns + 1;
        let couponHist = [...u.couponHistory];
        let couponsAvail = u.couponsAvailable;
        if (totalCheckIns % COUPON_MODULO === 0) {
          const cp = { id: genId('cp'), createdAt: nowTs, redeemedAt: null };
          couponHist.push(cp);
          couponsAvail++;
          commitAudit('coupon.create', { userId, couponId: cp.id }, authUserId);
        }
        commitAudit('checkin.add', { userId, checkInId: newCheckIn.id }, authUserId);
        return {
          ...u,
          checkIns: newCheckIns,
          totalCheckIns,
          couponsAvailable: couponsAvail,
          couponHistory: couponHist,
          lastCheckInAt: nowTs
        };
      })
    );
  }, [authUserId]);

  const deleteCheckIn = (userId, checkInId) => {
    setUsers(prev =>
      prev.map(u => {
        if (u.id !== userId) return u;
        const newChecks = u.checkIns.filter(c => c.id !== checkInId);
        const total = newChecks.length;
        const targetCount = Math.floor(total / COUPON_MODULO);
        const sorted = [...u.couponHistory].sort((a, b) => a.createdAt - b.createdAt);
        const rebuilt = sorted.slice(0, targetCount);
        const invalid = sorted.slice(targetCount).filter(c => c.redeemedAt);
        if (invalid.length)
          commitAudit('coupon.invalidate', { userId, couponIds: invalid.map(c => c.id) }, authUserId);
        const avail = rebuilt.filter(c => !c.redeemedAt).length;
        commitAudit('checkin.delete', { userId, checkInId }, authUserId);
        return {
          ...u,
          checkIns: newChecks,
          totalCheckIns: total,
          couponsAvailable: avail,
          couponHistory: rebuilt,
          lastCheckInAt: newChecks.length ? newChecks[newChecks.length-1].ts : 0
        };
      })
    );
  };

  const login = (email, password) => {
    const u = users.find(x => x.email.toLowerCase() === email.toLowerCase());
    if (!u || u.passwordHash !== password) throw new Error('Invalid credentials');
    if (!u.isActive) throw new Error('Account inactive');
    setAuthUserId(u.id);
    return u;
  };

  const logout = () => setAuthUserId(null);

  const resetUserCheckIns = (userId) => {
    updateUser(userId, u => ({
      ...u,
      lastCheckInAt: 0,
      totalCheckIns: 0,
      couponsAvailable: 0,
      couponHistory: [],
      checkIns: []
    }));
    commitAudit('user.resetChecks', { userId }, authUserId);
  };

  // ——— END Helper Functions ———

  return (
    <AppContext.Provider
      value={{
        users,
        setUsers,
        authUserId,
        login,
        logout,
        performCheckIn,
        redeemCoupon,
        deleteCheckIn,
        softDeleteUser,
        restoreUser,
        renameUser,
        addUser,
        findUser,
        auditLog,
        resetUserCheckIns
      }}
    >
      {children}
    </AppContext.Provider>
  );
}

const useApp = () => useContext(AppContext);
const useAuthUser = () => {
  const { authUserId, findUser } = useApp();
  return authUserId ? findUser(authUserId) : null;
};

// ---- UI Components ----

// AuthGate: ensures user is logged in
function AuthGate({ children }) {
  const user = useAuthUser();
  return user ? <>{children}</> : <LoginForm />;
}

// NavBar: navigation tabs
function NavBar({ current, onChange }) {
  const { logout } = useApp();
  return (
    <nav className="nav-bar">
      {['scan','progress','leaderboard','admin'].map(view => (
        <button
          key={view}
          className={current===view?'nav-item active':'nav-item'}
          onClick={()=>onChange(view)}
        >{view}</button>
      ))}
      <button onClick={()=>{logout(); toast.info('Logged out');}}>Logout</button>
    </nav>
  );
}

// Placeholder content views
function ScanView() {
  const user = useAuthUser();
  const { performCheckIn } = useApp();
  const scannerRef = useRef(null);
  const html5QrCodeRef = useRef(null);

  const [scanning, setScanning] = useState(false);
  const [status, setStatus] = useState('Ready');
  const [error, setError] = useState('');

  const cancelScanning = () => {
    if (html5QrCodeRef.current) {
      html5QrCodeRef.current.stop().catch(()=>{});
    }
    setScanning(false);
    setStatus('Ready');
    setError('');
  };

  const startScanning = () => {
    setError('');
    setScanning(true);
  };

  useEffect(() => {
    if (scanning && scannerRef.current) {
      const qr = new Html5Qrcode(scannerRef.current.id);
      html5QrCodeRef.current = qr;
      qr.start(
        { facingMode: 'environment' },
        { fps: 10, qrbox: { width: 360, height: 360 } },
        (decoded) => {
          try {
            performCheckIn(user.id, decoded);
            setStatus('Check-in successful');
            toast.success('Check-in recorded');
          } catch (e) {
            setError(e.message);
            toast.error(e.message);
          }
          cancelScanning();
        },
        (err) => {
          // ignore decode errors
        }
      ).catch(err => {
        setError('Camera error');
        setScanning(false);
      });
    }
    return () => {
      if (html5QrCodeRef.current) html5QrCodeRef.current.stop().catch(()=>{});
    };
  }, [scanning, user, performCheckIn]);

  if (scanning) {
    return (
      <div className="scan-view scanning fullscreen-scan">
        <div id="qr-region" ref={scannerRef} className="qr-region" style={{ width: '100%', height: '100vh' }} />
        <div className="qr-overlay">
          <div className="qr-box" />
        </div>
        <button onClick={cancelScanning} className="cancel-button">Cancel</button>
      </div>
    );
  }

  return (
    <div className="scan-view app-container">
      <h1>Check-In</h1>
      <p>Status: {status}</p>
      {error && <p className="error">{error}</p>}
      <button onClick={startScanning}>Start Scan</button>
      <div style={{ marginTop: '1em' }}>
        <input type="text" placeholder="Enter code" onKeyDown={e => e.key==='Enter' && performCheckIn(user.id, e.target.value)} />
      </div>
    </div>
  );
}

function ProgressView() {
  const user = useAuthUser();
  if (!user) return null;
  return (
    <div className="app-container">
      <h2>Progress</h2>
      <div className="card">
        <p><strong>Total Check-Ins:</strong> {user.totalCheckIns}</p>
        <p><strong>Coupons Available:</strong> {user.couponsAvailable}</p>
        <h3>History</h3>
        <ul className="checkin-list">
          {user.checkIns.slice().reverse().map(ci => (
            <li key={ci.id}>{new Date(ci.ts).toLocaleString()}</li>
          ))}
        </ul>
      </div>
    </div>
  );
}
function LeaderboardView() { return <div>LeaderboardView placeholder</div>; }
function AdminView() { return <div>AdminView placeholder</div>; }

export default App;
